// To heapify a subtree rooted with node i which is
// an index in arr[]. n is size of heap
export function heapify(arr, n, i, animations) {
  let largest = i; // Initialize largest as root
  let l = 2 * i + 1; // left = 2*i + 1
  let r = 2 * i + 2; // right = 2*i + 2

  // If left child is larger than root
  if (l < n && arr[l] > arr[largest]) largest = l;

  // If right child is larger than largest so far
  if (r < n && arr[r] > arr[largest]) largest = r;

  // If largest is not root
  if (largest != i) {
    animations.push([i, largest, 1]);
    animations.push([i, largest, 2]);

    animations.push([i, largest, 0]);
    let k = arr[i];
    arr[i] = arr[largest];
    arr[largest] = k;

    // Recursively heapify the affected sub-tree
    heapify(arr, n, largest, animations);
  }
}

// main function to do heap sort
export function heapsort(arr) {
  const animations = [];
  let n = arr.length;
  // Build heap (rearrange array)
  for (let i = n / 2 - 1; i >= 0; i--) heapify(arr, n, i, animations);

  // One by one extract an element from heap
  for (let i = n - 1; i > 0; i--) {
    animations.push([0, i, 1]);
    animations.push([0, i, 2]);
    animations.push([0, i, 0]);
    let k = arr[i];
    arr[i] = arr[0];
    arr[0] = k;

    // call max heapify on the reduced heap
    heapify(arr, i, 0, animations);
  }

  return animations;
}
